"use client"

import {
  Reasoning,
  ReasoningContent,
  ReasoningTrigger,
} from "@/components/prompt-kit/reasoning";
import { Markdown } from "@/components/prompt-kit/markdown";

const BUBBLE_CONFIG = {
  width: { min: 60, maxPercentOfContainer: 0.85 },
  height: { min: 40, lineHeight: 24, paddingVertical: 20 },
  padding: { horizontal: 20, vertical: 10 },
  borderRadius: 24,
};

export interface Message {
  role: "user" | "assistant";
  content: string;
  reasoning?: string;
  isStreaming?: boolean;
  imageUrl?: string;
}

export function LoadingDot() {
  return (
    <div className="flex items-center">
      <span 
        className="h-2 w-2 rounded-full bg-foreground"
        style={{
          animation: "scaleUpDown 1s ease-in-out infinite",
        }}
      />
      <style>{`
        @keyframes scaleUpDown {
          0%, 100% { transform: scale(1); }
          50% { transform: scale(1.8); }
        }
      `}</style>
    </div>
  );
}

export function ReasoningSection({ reasoning, isStreaming = false }: { reasoning: string; isStreaming?: boolean }) {
  if (!reasoning) return null;

  return (
    <Reasoning isStreaming={isStreaming}>
      <ReasoningTrigger />
      <ReasoningContent>{reasoning}</ReasoningContent>
    </Reasoning>
  );
}

export function AssistantMessage({ 
  content, 
  reasoning, 
  isStreaming
}: { 
  content: string; 
  reasoning?: string; 
  isStreaming?: boolean;
}) {
  return (
    <div className="w-full min-w-0 flex-1 overflow-hidden">
      {reasoning && (
        <ReasoningSection reasoning={reasoning} isStreaming={isStreaming} />
      )}
      <Markdown className="prose prose-h1:text-2xl prose-h2:text-xl prose-h3:text-lg prose-h4:text-base prose-h5:text-sm prose-h6:text-xs dark:prose-invert max-w-none text-base font-medium text-black dark:text-white break-words overflow-hidden">
        {content}
      </Markdown>
    </div>
  );
}

export function UserMessage({ 
  content, 
  imageUrl, 
  index 
}: { 
  content: string; 
  imageUrl?: string; 
  index: number;
}) {
  const getBubbleStyle = (textLength: number) => {
    const estimatedLines = Math.ceil(textLength / 40);
    const estimatedHeight = Math.max(
      BUBBLE_CONFIG.height.min,
      estimatedLines * BUBBLE_CONFIG.height.lineHeight + BUBBLE_CONFIG.height.paddingVertical
    );
    
    let maxWidth: string;
    if (textLength <= 20) {
      maxWidth = "fit-content";
    } else if (textLength <= 50) {
      maxWidth = "50%";
    } else if (textLength <= 100) {
      maxWidth = "65%";
    } else {
      maxWidth = `${BUBBLE_CONFIG.width.maxPercentOfContainer * 100}%`;
    }

    return {
      minWidth: `${BUBBLE_CONFIG.width.min}px`,
      maxWidth,
      minHeight: `${BUBBLE_CONFIG.height.min}px`,
      padding: `${BUBBLE_CONFIG.padding.vertical}px ${BUBBLE_CONFIG.padding.horizontal}px`,
      borderRadius: `${BUBBLE_CONFIG.borderRadius}px`,
    };
  };

  const bubbleStyle = getBubbleStyle(content.length);

  return (
    <div className="flex justify-end">
      <div 
        className="bg-[#ededed] dark:bg-[#2a2a2a] text-stone-900 dark:text-stone-100"
        style={bubbleStyle}
      >
        {imageUrl && (
          <img 
            src={imageUrl} 
            alt="Attached" 
            className="max-w-full max-h-48 rounded-lg mb-2 object-contain"
            data-testid={`message-image-${index}`}
          />
        )}
        <p className="whitespace-pre-wrap text-base font-medium">{content}</p>
      </div>
    </div>
  );
}
