"use client"

import { useState } from "react"

interface CodeBlockProps {
  children: React.ReactNode
  className?: string
}

export function CodeBlock({ children, className }: CodeBlockProps) {
  return (
    <div className={`relative rounded-lg bg-fd-secondary/50 overflow-hidden ${className || ""}`}>
      {children}
    </div>
  )
}

interface CodeBlockCodeProps {
  code: string
  language?: string
  className?: string
}

export function CodeBlockCode({ code, language = "plaintext", className }: CodeBlockCodeProps) {
  return (
    <pre className={`overflow-x-auto p-4 text-sm m-0 ${className || ""}`}>
      <code className="text-fd-secondary-foreground" data-language={language}>
        {code}
      </code>
    </pre>
  )
}

interface CodeBlockHeaderProps {
  children?: React.ReactNode
  className?: string
}

export function CodeBlockHeader({ children, className }: CodeBlockHeaderProps) {
  return (
    <div className={`flex items-center justify-between px-4 py-2 border-b border-fd-border bg-fd-muted/30 ${className || ""}`}>
      {children}
    </div>
  )
}

interface CodeBlockCopyButtonProps {
  code: string
  className?: string
}

export function CodeBlockCopyButton({ code, className }: CodeBlockCopyButtonProps) {
  const [copied, setCopied] = useState(false)

  const handleCopy = async () => {
    try {
      await navigator.clipboard.writeText(code)
      setCopied(true)
      setTimeout(() => setCopied(false), 2000)
    } catch (err) {
      console.error("Failed to copy:", err)
    }
  }

  return (
    <button
      onClick={handleCopy}
      className={`p-1.5 rounded-md text-fd-muted-foreground hover:text-fd-foreground hover:bg-fd-muted/50 transition-colors ${className || ""}`}
      title={copied ? "Copied!" : "Copy Code"}
    >
      {copied ? (
        <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
          <path d="M20 6L9 17l-5-5" />
        </svg>
      ) : (
        <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
          <rect x="9" y="9" width="13" height="13" rx="2" ry="2" />
          <path d="M5 15H4a2 2 0 01-2-2V4a2 2 0 012-2h9a2 2 0 012 2v1" />
        </svg>
      )}
    </button>
  )
}
